.navbar {
    width: 100%;
    height: 65px;
    background-color: hsla(210, 30%, 24%, 1);
    color: white;
    padding: 0.75rem 2% 0 2%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    font-size: 16px;
}

.container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.dark-nav {
    background-color: hsla(210, 30%, 24%, 1);
    transition: 0.5s;
}

.text {
    display: flex;
    font-family: cursive;
    font-size: 30px;
    font-weight: bold;
}

.Logo {
    width: 15%;
}

.logo {
    padding-top: 0;
    width: 40px;
}

.menu-toggle {
    display: none; /* Hide by default on larger screens */
    cursor: pointer;
}

.menu-toggle i {
    font-size: 24px;
}

.navbar-menu {
    display: flex;
    list-style: none;
    gap: 8px;
    margin: 0;
    font-size: 16px;
}

.navbar-menu li {
    padding: 8px 12px;
    border-radius: 18px;
    transition: background-color 0.3s ease;
}

.navbar-menu li:hover {
    background-color: hsla(210, 30%, 39%, 1);
    cursor: pointer;
}

/* Media query for smaller screens */
@media (max-width: 768px) {
    .menu-toggle {
        display: block; /* Show toggle button */
    }

    .navbar-menu {
        flex-direction: column;
        position: absolute;
        top: 65px;
        left: 0;
        width: 100%;
        background-color: hsla(210, 30%, 24%, 1);
        transition: transform 0.3s ease;
        transform: scaleY(0); /* Initially hidden */
        transform-origin: top;
        z-index: -1;
    }

    .navbar-menu.open {
        transform: scaleY(1); /* Show menu */
        z-index: 1;
    }

    .navbar-menu li {
        width: 100%;
        text-align: center;
    }
}
